<template>
    <div class="login-wrap">
        <div class="ms-login">
            <div class="ms-title">后台管理系统</div>
            <el-form :model="param" :rules="rules" ref="login" label-width="0px" class="ms-content">
                <el-form-item prop="username">
                    <el-input v-model="param.username" placeholder="username">
                        <template #prepend>
                            <el-button icon="el-icon-user"></el-button>
                        </template>
                    </el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input
                        type="password"
                        placeholder="password"
                        v-model="param.password"
                        @keyup.enter="submitForm()"
                    >
                        <template #prepend>
                            <el-button icon="el-icon-lock"></el-button>
                        </template>
                    </el-input>
                </el-form-item>
                <div class="login-btn">
                    <el-button type="primary" @click="submitForm()">登录</el-button>
                </div>
                <p class="login-tips"></p>
            </el-form>
        </div>
    </div>
</template>
<script>
import { login } from '../../api/manage';
import md5 from "md5"
import { mapMutations } from "vuex"
export default {
    data() {
        return {
            param: {
                username: "",
                password: "",
                verifycode:'1234'
            },
            rules: {
                username: [
                    { required: true, message: "请输入用户名", trigger: "blur" }
                ],
                password: [
                    { required: true, message: "请输入密码", trigger: "blur" }
                ]
            }
        };
    },
    created() {
    },
    methods: {
        ...mapMutations(["setUserInfo"]),
        submitForm() {
            this.$refs.login.validate(valid => {
                if(valid){
                    let userpwd =md5(this.param.password).slice(0,-2).toUpperCase()
                    login({
                        username:this.param.username,
                        userpwd:userpwd,
                        verifycode:this.param.verifycode,
                        module:'1000',
                        command:'1001',
                        }).then(data=>{
                            console.log(data);
                        if(data.status ==10000){
                            // 把数据存储到vuex中
                            this.setUserInfo({userinfo:data.userinfo})
                            // 为了防止刷新，数据丢失，需要把数据保存到localstorage
                            localStorage.setItem("token",data.token)
                            localStorage.setItem("sign",data.sign)
                            localStorage.setItem("userid",data.userinfo.userid)
                            localStorage.setItem("userinfo",JSON.stringify(data.userinfo))
                            this.$message({
                                message: '登录成功',
                                type: 'success'
                            });
                            this.$router.push("/potentialCustomers");
                        }else{
                            this.$message({
                                message: '用户不存在或密码错误',
                                type: 'error'
                            });
                        }
                    })
                }
            })
            
            
        }
    }
};
</script>

<style scoped>
.login-wrap {
    position: relative;
    width: 100%;
    height: 100%;
    background-image: url(../../assets/img/login-bg.jpg);
    background-size: 100%;
}
.ms-title {
    width: 100%;
    line-height: 50px;
    text-align: center;
    font-size: 20px;
    color: #fff;
    border-bottom: 1px solid #ddd;
}
.ms-login {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 350px;
    margin: -190px 0 0 -175px;
    border-radius: 5px;
    background: rgba(255, 255, 255, 0.3);
    overflow: hidden;
}
.ms-content {
    padding: 30px 30px;
}
.login-btn {
    text-align: center;
}
.login-btn button {
    width: 100%;
    height: 36px;
    margin-bottom: 10px;
}
.login-tips {
    font-size: 12px;
    line-height: 30px;
    color: #fff;
}
</style>